<template>
  <div class="mobilenet_v2">
    <div>
      <canvas
        id="mobilenet-v2-hidden"
        ref="mobileNetV2Hidden"
        width="224"
        height="224"
      />
    </div>
    <input ref="uploader" type="file" @change="handleFileChange" />
    <div>
      {{ answer }}
    </div>
  </div>
</template>

<script>
import * as tf from '@tensorflow/tfjs';
import labels from './ImageNetLabels';

export default {
  name: 'MobileNetV2',
  data() {
    return {
      model: null,
      answer: null,
    };
  },

  methods: {
    handleFileChange() {
      if (!this.$refs.uploader.files.length) {
        return;
      }

      this.answer = null;

      const hiddenCanvas = this.$refs.mobileNetV2Hidden;
      const file = this.$refs.uploader.files[0];
      const imgUrl = URL.createObjectURL(file);
      const img = new Image();
    
      img.onload = () => {
        URL.revokeObjectURL(img.src);
        hiddenCanvas.getContext("2d").drawImage(img, 0, 0, 224, 224);

        this.makePrediction(hiddenCanvas);
      };

      img.src = imgUrl;
    },

    async makePrediction(data) {
      const x = tf.div(tf.fromPixels(data).asType('float32'), 255.).expandDims();
      const prediction = this.model.predict(x).squeeze();
      const [predictionIndex] = await tf.argMax(prediction).data();
      this.answer = labels[predictionIndex];
    },
  },

  async created() {
    this.model = await tf.loadModel('models/mobilenet_v2/model.json');
  }
}
</script>

